import axios from 'axios';
import { useEffect, useState } from 'react';
import './App.css';
import { Script } from "./Script/Script";

function App() {
  const [state, setState] = useState('')
  //const find = "?search=90"
  useEffect(() => {
    axios("https://6375371508104a9c5f95411e.mockapi.io/Bascet?search=90")
    .then(res => setState(res.data))
  }, [])
  console.log('state', state)
  return (
    <div className="App">
      {state.map((data, index) => {
        return (
          <div>
            {data.name}
            <img src={data.avatar} alt="face"/>
          </div>
        )
      })}
      <Script />
    </div>
  );
}

export default App;
